import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Alert, Button, Modal, ModalBody, ModalFooter } from 'reactstrap'
import axios from 'axios'
import Spinner from '../../Spinner/Spinner.js'
import Summary from '../Controls/Summary'
import { resetBurger } from '../../../redux/actionCreators'
import { NavLink } from 'react-router-dom'


const mapStateToProps = state => {
    return {
        totalPrice: state.totalPrice,
        ingredients: state.ingredients,
        canOrder: state.canOrder,
        ingredientsPrices: state.ingredientsPrices,
        userId: state.userId,
        token: state.token

    }
}

const mapDispatchToProps = dispatch => {
    return {
        resetBurger: () => dispatch(resetBurger())
    }
}


export class Checkout extends Component {
    constructor(props) {
        super(props)

        this.handleInputChange = this.handleInputChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
    }

    state = {
        values: {
            deliveryAddress: '',
            phone: '',
            paymentType: "Cash On Delivery"
        },
        orderSuccess: false,
        alertColor: '',
        isLoading: false,
        isModalOpen: false,
        orderMess: ''


    }

    handleInputChange = (event) => {
        this.setState({
            values: {
                ...this.state.values,
                [event.target.name]: event.target.value

            }
        })
    }

    handleSubmit = (event) => {
        event.preventDefault();
        this.setState({
            isLoading: true
        })

        const newOrder = {
            customer: this.state.values,
            ingredients: this.props.ingredients,
            orderDate: new Date(),
            totalPrice: this.props.totalPrice,
            userId: this.props.userId
        }

        axios.post("https://burger-builder-bc974-default-rtdb.firebaseio.com/wow-burger.json?auth=" + this.props.token, newOrder)
            .then(res => {
                //   console.log(res)
                this.setState({
                    orderSuccess: true,
                    alertColor: 'success',
                    isLoading: false,
                    orderMess: "Order Submitted Successfully",
                    isModalOpen: true,
                })
            })
            .catch(err => {
                console.log(err)
                this.setState({
                    alertColor: 'danger',
                    isLoading: false,
                    orderMess: "Somethig Went Wrong! Try Again.",
                    isModalOpen: true,
                    orderSuccess: false
                })
            })
    }

    handleModal = () => {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        })
    }

    render() {
        let form = null

        if (this.state.isLoading) {
            return <Spinner />
        } else {
            form = <form onSubmit={this.handleSubmit} >
                <input
                    className="form-control"
                    type="textarea"
                    name="deliveryAddress"
                    value={this.state.values.deliveryAddress}
                    onChange={this.handleInputChange}
                    placeholder="Your Delivery Address"
                />
                <br />
                <input
                    className="form-control"
                    type="text"
                    name="phone"
                    value={this.state.values.phone}
                    onChange={this.handleInputChange}
                    placeholder="Phone Number"
                />
                <br />
                <select
                    className="form-control"
                    type='select'
                    onChange={this.handleInputChange}
                    name="paymentType"
                    value={this.state.values.paymentType}
                >
                    <option value="Cash On Delivery">Cash On Delivery</option>
                    <option value='bKash'> bKash</option>
                </select>
                <br />
                <div>
                    <Button
                        disabled={!this.props.canOrder}
                        className="btn-success" type='submit'>
                        Submit Order
                    </Button>
                </div>
            </form>
        }


        return (
            <div>
                <h4 className="p-2 bg-primary text-white"
                >Total payable amount: {this.props.totalPrice} </h4>
                <h5 className="text-start">Coplete the order:</h5>
                {form}
                <Modal isOpen={this.state.isModalOpen} >
                    <ModalBody>
                        <Alert color={this.state.alertColor} > <h5>{this.state.orderMess}</h5> </Alert>
                    </ModalBody>
                    <ModalFooter>
                        {this.state.orderSuccess ?
                            <NavLink
                                className="btn btn-success"
                                to='/'
                                onClick={this.props.resetBurger}
                            >Order Again
                            </NavLink>
                            : <Button
                                onClick={this.handleModal}
                            >Try again.</Button>}
                    </ModalFooter>
                </Modal>

            </div>
        )
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Checkout)