import React, { useState } from 'react'
import { useFormik } from 'formik';
import './Auth.css'
import { Alert, Button } from 'reactstrap'
import { auth } from '../../redux/authActionCreators.js'
import { connect } from 'react-redux'
import Spinner from '../Spinner/Spinner';

const mapStateToProps = state => {
    return {
        authLoading: state.authLoading,
        authFailedMsg: state.authFailedMsg
    }
}

const mapDispatchToProps = dispatch => {
    return {
        auth: (email, password, mode) => dispatch(auth(email, password, mode))
    }
}

const Auth = (props) => {
    let [mode, setMode] = useState('Sign Up')

    let changeMode = mode === 'Sign Up' ? 'Login' : 'Sign Up'

    let modeHandler = () => {
        setMode(changeMode)
    }

    const validate = values => {
        const errors = {};

        if (!values.email) {
            errors.email = 'Required'
            errors.emailAlertColor = 'danger'
        } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
            errors.email = 'Invalid email address'
            errors.emailAlertColor = 'warning'
        }

        if (!values.password) {
            errors.password = 'Required'
            errors.passAlert = 'danger'
        } else if (values.password.length < 6) {
            errors.password = "Password must be 6 charecter long"
            errors.passAlert = 'warning'
        }


        if (mode === 'Sign Up') {
            if (!values.confirmPassword) {
                errors.confirmPassword = 'Required'
                errors.confirmPasswordAlert = 'danger'
            } else if (values.password !== values.confirmPassword) {
                errors.confirmPassword = 'Password did not match!'
                errors.confirmPasswordAlert = 'warning'
            }
        }

        return errors
    }

    let authForm = useFormik({
        initialValues: {
            email: '',
            password: '',
            confirmPassword: ''
        },
        validate,
        onSubmit: values => {
            let email = values.email

            const password = values.password

            props.auth(email, password, mode)

        }
    })
    // console.log(auth)
    let err = null

    if (props.authFailedMsg) {
        err = <Alert color="danger">{props.authFailedMsg} </Alert>
    }

    let form = null

    if (props.authLoading) {
        form = <Spinner />
    } else {
        form = <form onSubmit={authForm.handleSubmit}  >
            <div>
                <label htmlFor='email'>Email:</label>
                <input
                    id="email"
                    className="form-control"
                    name="email"
                    onChange={authForm.handleChange}
                    value={authForm.values.email}
                    placeholder="name@domain.com"
                />

                {authForm.errors.email ?
                    <Alert color={authForm.errors.emailAlertColor}  > {authForm.errors.email} </Alert>
                    : null
                }

            </div><br />
            <div>
                <label htmlFor='password'>Password</label>
                <input
                    id="password"
                    className="form-control"
                    name="password"
                    onChange={authForm.handleChange}
                    value={authForm.values.password}
                    placeholder="123456"
                />
                {
                    authForm.errors.password ?
                        <Alert color={authForm.errors.passAlert} > {authForm.errors.password}</Alert>
                        : null
                }
            </div><br />
            {
                mode === 'Sign Up' ?
                    <div>
                        <label htmlFor='confirmPassword'>Confirm Password</label>
                        <input
                            id="confirmPassword"
                            className="form-control"
                            name="confirmPassword"
                            onChange={authForm.handleChange}
                            value={authForm.values.confirmPassword}
                            placeholder="123456"
                        />
                        {
                            authForm.errors.confirmPassword ?
                                <Alert color={authForm.errors.confirmPasswordAlert} > {authForm.errors.confirmPassword}</Alert>
                                : null
                        }
                    </div>
                    : <div></div>
            }

            <br />

            <button className="btn btn-success" type='submit' >
                {
                    mode === 'Sign Up' ? 'Sign Up' : 'Login'
                }
            </button>
        </form>
    }

    return (
        <div className="d-flex justify-content-center" >
            <div className="text-start col-12 col-sm-10 col-md-7" >
                <div className="changeSignMode">
                    <Button
                        onClick={modeHandler}
                        className="w-100 "
                        style={{ backgroundColor: "rgb(215,15,100)" }}
                    >
                        Change To <span> </span>
                        {
                            mode === 'Sign Up' ? 'Login' : 'Sing Up'
                        }
                        !
                    </Button>

                    {err}

                    {form}
                </div>
                <div>
                    { }
                </div>

            </div>
        </div>

    )
}

export default connect(mapStateToProps, mapDispatchToProps)(Auth)