import React from 'react'
import { Card, CardHeader } from 'reactstrap'
import dateFormat from "dateformat";

const Order = (props) => {
    // console.log(props)
    let orderDate = props.order.date
    return (
        <div>
            <Card className="m-2">
                <CardHeader>
                    <div className="row">
                        <div className="col-12 col-md-6 text-center  text-md-start">
                            <p><strong>Delivery Address:</strong> {props.order.customer.deliveryAddress} </p>
                            <p><strong>Phone:</strong> {props.order.customer.phone} </p>
                            <p><strong>Payment Type:</strong> {props.order.customer.paymentType} </p>
                            <p><strong>Order Id:</strong> {props.order.id} </p>
                        </div>
                        <div className="col-12 col-md-6 text-md-end text-center">
                            <p className="p-1"
                                style={{
                                    border: '1px solid grey',
                                    borderRadius: "5px",
                                    backgroundColor: "#D70F64",
                                    color: "#fff"
                                }}
                            >
                                Order Date: {dateFormat(orderDate, "mmm, yyyy, h:MM: TT")} </p>
                            <div>
                                {
                                    props.order.ingredients.map(item => {
                                        return (
                                            <span key={item.type} style={{ border: "1px solid grey", padding: "5px", borderRadius: "5px", margin: "2px" }} >
                                                <span className="text-capitalize">
                                                    <strong>    {item.type}</strong>
                                                </span>
                                                <span> </span>
                                                <span style={{ color: "#D70F64" }}>
                                                    <strong>{item.amount}x</strong> </span>
                                            </span>
                                        )
                                    })
                                }
                            </div>
                            <hr />
                            <div>
                                <h5>Total Price: {props.order.price}/-BDT </h5>
                            </div>
                        </div>
                    </div>
                </CardHeader>
            </Card>
        </div>
    )
}

export default Order