import React from 'react'
import {
    Navbar,
    NavbarBrand,
    Nav,
    NavItem
} from 'reactstrap'
import Logo from '../../assets/logo.png'
import { NavLink } from 'react-router-dom'
import './Header.css'
import { connect } from 'react-redux'

const mapStateToProps = state => {
    return {
        token: state.token,
        userId: state.userId
    }
}


const Header = (props) => {
    // console.log(props)

    let showNav = null

    if (!props.token) {
        // login
        showNav = <Nav style={{}} className="mr-md-5">
            {/* <NavItem>
                <NavLink className="nav-link" style={{ color: '#fff' }} to='/'>
                    BurgerBuilder
                </NavLink>
            </NavItem>
            <NavItem>
                <NavLink className="nav-link" style={{ color: '#fff' }} to='/orders'>
                    Orders
                </NavLink>
            </NavItem> */}
            <NavItem>
                <NavLink className="nav-link" style={{ color: '#fff' }} to='/login'>
                    Login
                </NavLink>
            </NavItem>
        </Nav>
    } else {
        showNav = <Nav style={{}} className="mr-md-5">
            <NavItem>
                <NavLink className="nav-link" style={{ color: '#fff' }} to='/'>
                    BurgerBuilder
                </NavLink>
            </NavItem>
            <NavItem>
                <NavLink className="nav-link" style={{ color: '#fff' }} to='/orders'>
                    Orders
                </NavLink>
            </NavItem>
            <NavItem>
                <NavLink className="nav-link" style={{ color: '#fff' }} to='/logout'>
                    Logout
                </NavLink>
            </NavItem>
        </Nav>
    }

    return (
        <div style={{ backgroundColor: "#d70f64" }} >
            <Navbar className="header_nav" style={{
                backgroundColor: "#d70f64",
                padding: "5px",
                color: "#fff"
            }}>
                <NavbarBrand style={{ color: '#fff', padding: "0" }}
                    className="mr-auto ml-md-5  d-block" href="/" >
                    <img className="nav_logo" alt={Logo} style={{ height: "60px" }} src={Logo} />
                </NavbarBrand>
                {showNav}
            </Navbar>
        </div>
    )
}

export default connect(mapStateToProps)(Header)