import * as actionTypes from './actionTypes.js'
import axios from 'axios'


const authSucess = (token, userId) => {
    return {
        type: actionTypes.AUTH_SUCCESS,
        payload: {
            token: token,
            userId: userId
        }
    }
}

const authLoading = isLoading => {
    return {
        type: actionTypes.AUTH_LOADING,
        payload: isLoading
    }
}

const authFailed = (errMess) => {
    return {
        type: actionTypes.AUTH_FAILED,
        payload: errMess
    }
}

export const auth = (email, password, mode) => dispatch => {
    dispatch(authLoading(true))
    const authData = {
        email: email,
        password: password,
        returnSecureToken: true
    }

    const API_KEY = 'AIzaSyAbxQUwpAfqGLM3NBN5F1gn7Uu1DrrgXew'
    let auth_url = null

    if (mode === 'Sign Up') {
        auth_url = "https://identitytoolkit.googleapis.com/v1/accounts:signUp?key="
    } else if (mode === 'Login') {
        auth_url = "https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key="
    }


    axios.post(auth_url + API_KEY, authData)
        .then(res => {
            console.log(res)
            let expirationTime = new Date(new Date().getTime() + res.data.expiresIn * 1000)
            dispatch(authSucess(res.data.idToken, res.data.localId))

            localStorage.setItem('token', res.data.idToken)
            localStorage.setItem('userId', res.data.localId)
            localStorage.setItem('expirationTime', expirationTime)
            dispatch(authLoading(false))

        })
        .catch(err => {
            console.log(err.response.data.error.message)
            dispatch(authLoading(false))
            dispatch(authFailed(err.response.data.error.message))
        })

}

export const auth_logout = () => {

    localStorage.removeItem('token')
    localStorage.removeItem('userId')
    localStorage.removeItem('expirationTime')

    return {
        type: actionTypes.AUTH_LOGOUT
    }
}

export const authCheck = () => dispatch => {
    const token = localStorage.getItem('token')
    const userId = localStorage.getItem('userId')

    if (!token) {
        dispatch(auth_logout())
    } else {
        const expirationTime = localStorage.getItem('expirationTime')
        if (expirationTime <= new Date().getTime()) {
            dispatch(auth_logout())
        } else {
            dispatch(authSucess(token, userId))
        }
    }
}