import React from 'react'
import { Button } from 'reactstrap'

const Summary = (props) => {
    // console.log(props.disableLess)
    return (
        <div>
            <table className="controlTable w-100" >
                <tbody>
                    <tr>
                        <th>Item</th>
                        <th>Unit Price</th>
                        <th>Amount</th>
                        <th>Sub Total</th>
                        <th>Less</th>
                        <th>More</th>
                    </tr>
                    {
                        props.ingredients.map(item => {
                            let unitPrice = props.prices[item.type]
                            let amount = item.amount
                            let subtotal = unitPrice * amount
                            return <tr key={item.type}>
                                <td> <span style={{ textTransform: "capitalize" }} >{item.type}</span> </td>
                                <td> {props.prices[item.type]} </td>
                                <td> {item.amount} </td>
                                <td> {subtotal} </td>
                                <td> <Button

                                    onClick={() => props.remove(item.type)}
                                    className="btn-danger"> Less </Button>  </td>
                                <td> <Button
                                    onClick={() => props.add(item.type)}
                                    className="btn-success"> More </Button>  </td>
                            </tr>
                        })
                    }
                    <tr>
                        <td>Total</td>
                        <td></td>
                        <td></td>
                        <td>{props.totalPrice}</td>
                        <td></td>
                        <td></td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}

export default Summary