import React, { Component } from 'react'
import { connect } from 'react-redux'
import Burger from './Burger/Burger.js'
import Controls from './Controls/Controls.js'
import { addIngredients, removeIngredients, updateCanOrder } from '../../redux/actionCreators.js'
import { Modal, ModalBody, ModalFooter, Button, ModalHeader } from 'reactstrap'
import Summary from './Controls/Summary.js'
import { NavLink } from 'react-router-dom'

const INGREDIENTS_PRICE = {
    'salad': 20,
    'meat': 70,
    'cheese': 50
}



const mapStateToProps = state => {

    return {
        ingredients: state.ingredients,
        totalPrice: state.totalPrice,
        canOrder: state.canOrder

    }
}


const mapDispatchToProps = dispatch => {
    return {
        addIngredients: (igtype) => dispatch(addIngredients(igtype)),
        removeIngredients: (igtype) => dispatch(removeIngredients(igtype)),
        updateCanOrder: () => dispatch(updateCanOrder())
    }
}

export class BurderBuilder extends Component {
    state = {
        isOrderModalOpen: false
    }

    addIngredients = type => {
        this.props.addIngredients(type)
        this.props.updateCanOrder()
    }

    removeIngredients = type => {
        this.props.removeIngredients(type)
        this.props.updateCanOrder()
    }

    orderModalHandle = () => {
        this.setState({
            isOrderModalOpen: !this.state.isOrderModalOpen
        })
    }

    render() {
        //   console.log(this.props.canOrder)
        return (
            <div>
                <div className="row">
                    <div className="col-12 col-md-4 pb-3">
                        <Burger ingredients={this.props.ingredients} />
                    </div>
                    <div className="col-12 col-md-8">
                        <Controls
                            canOrder={this.props.canOrder}
                            add={this.addIngredients}
                            remove={this.removeIngredients}
                            totalPrice={this.props.totalPrice}
                            ingredients={this.props.ingredients}
                            prices={INGREDIENTS_PRICE}
                            orderModal={this.orderModalHandle}
                        />
                    </div>
                </div>
                <div>
                    <Modal isOpen={this.state.isOrderModalOpen} >
                        <ModalHeader>
                            <>Total Price: {this.props.totalPrice} </>
                        </ModalHeader>
                        <ModalBody>
                            <Summary
                                totalPrice={this.props.totalPrice}
                                add={this.addIngredients}
                                remove={this.removeIngredients}
                                ingredients={this.props.ingredients}
                                prices={INGREDIENTS_PRICE}
                            />
                        </ModalBody>
                        <ModalFooter>
                            <NavLink to="/checkout" className="btn btn-success" >
                                Go to Checkout
                            </NavLink>
                            <Button
                                className="btn-danger"
                                onClick={this.orderModalHandle}
                            >Close</Button>
                        </ModalFooter>

                    </Modal>
                </div>
            </div>
        )
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(BurderBuilder)