import Main from './Components/Main';
import './App.css';
import { BrowserRouter } from 'react-router-dom'
import { Provider } from 'react-redux'
import { wowStore } from './redux/store.js'

function App() {
  document.title = 'Wow Burger'
  return (
    <div className="App">
      <Provider store={wowStore} >
        <BrowserRouter basename='/burger-builder' >
          <Main />
        </BrowserRouter>
      </Provider>


    </div>
  );
}

export default App;
