import React, { Component } from 'react'
import Header from './Header/Header'
import BurderBuilder from './BurgerBuilder/BurderBuilder'
import Orders from './BurgerBuilder/Orders/Orders'
import Checkout from './BurgerBuilder/Checkout/Checkout'
import { Routes, Route, Navigate } from 'react-router-dom'
import Auth from './Auth/Auth.js'
import { auth, authCheck } from '../redux/authActionCreators'
import { connect } from 'react-redux'
import Logout from './Auth/Logout'

const mapStateToProps = state => {
    return {
        token: state.token,
        userId: state.userId
    }
}

const mapDispatchToProps = dispatch => {
    return {
        authCheck: () => dispatch(authCheck())
    }
}

export class Main extends Component {


    componentDidMount() {
        this.props.authCheck()
    }
    render() {

        let showRoutes = null

        if (!this.props.token) {
            showRoutes = <Routes>
                <Route path="/" element={<BurderBuilder />} />
                <Route path="/login" element={<Auth />} />
                <Route path="*" element={<Navigate to="/login" replace />} />

            </Routes>
        } else {
            showRoutes = <Routes>
                <Route path="/" element={<BurderBuilder />} />
                <Route path="/orders" element={<Orders />} />
                <Route path="/logout/*" element={<Logout />} />
                <Route path="/checkout" element={<Checkout />} />
                <Route path="*" element={<Navigate to="/" replace />} />
            </Routes>
        }

        return (
            <div>
                <Header />
                <div className="container pt-5">
                    {showRoutes}
                </div>
            </div>
        )
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Main)