import React from 'react'
import Top from '../../../assets/images/top.png'
import Bottom from '../../../assets/images/bottom.png'
import Salad from '../../../assets/images/salad.png'
import Cheese from '../../../assets/images/cheese.png'
import Meat from '../../../assets/images/meat.png'
import './Ingredients.css'



const Ingredients = (props) => {
    let showIngredients = null

    switch (props.type) {
        case 'top':
            showIngredients = <div> <img src={Top} /> </div>
            break;
        case 'bottom':
            showIngredients = <div> <img src={Bottom} /> </div>
            break;
        case 'salad':
            showIngredients = <div> <img src={Salad} /> </div>
            break;
        case 'cheese':
            showIngredients = <div> <img src={Cheese} /> </div>
            break;
        case 'meat':
            showIngredients = <div> <img src={Meat} /> </div>
            break;
        default:
            showIngredients = null
    }

    return (
        <div className="ingredients">
            {showIngredients}
        </div>
    )
}

export default Ingredients
