import * as actionTypes from './actionTypes.js'
import axios from 'axios'




export const updateCanOrder = () => {
    return {
        type: actionTypes.UPDATE_CANORDER
    }
}




export const addIngredients = type => {
    return {
        type: actionTypes.ADD_INGREDIENTS,
        payload: type
    }
}


export const removeIngredients = type => {
    return {
        type: actionTypes.REMOVE_INGREDIENTS,
        payload: type
    }
}

export const resetBurger = () => {
    return {
        type: actionTypes.RESET_BURGER
    }
}

const loadOrder = order => {
    return {
        type: actionTypes.LOAD_ORDER,
        payload: order
    }
}

const orderLoadingFailed = (errMess) => {
    return {
        type: actionTypes.ORDER_LOADING_FAILED,
        payload: errMess
    }
}

export const fetchOrder = (token, userId) => dispatch => {
    const queryParams = '&orderBy="userId"&equalTo="' + userId + '"';
    axios.get('https://burger-builder-bc974-default-rtdb.firebaseio.com/wow-burger.json?auth=' + token + queryParams)
        .then(res => {
            dispatch(loadOrder(res.data))
        })
        .catch(err => {
            console.log(err)
            dispatch(orderLoadingFailed(err.message))
        })
}

