import * as actionTypes from './actionTypes.js'



const INGREDIENTS_PRICE = {
    salad: 20,
    meat: 70,
    cheese: 50
}

const INITIAL_STATE = {
    ingredients: [
        { type: 'salad', amount: 0 },
        { type: 'meat', amount: 0 },
        { type: 'cheese', amount: 0 }
    ],
    totalPrice: 80,
    canOrder: false,
    ingredientsPrices: INGREDIENTS_PRICE,
    orders: [],
    orderLoading: true,
    orderError: false,
    orderFailedMess: '',
    token: null,
    userId: null,
    authLoading: false,
    authFailedMsg: null,

}




export const Reducer = (state = INITIAL_STATE, action) => {
    let newingredients = [...state.ingredients]
    let newtotalPrice = state.totalPrice

    switch (action.type) {

        case actionTypes.ADD_INGREDIENTS:

            for (let item of newingredients) {
                if (item.type === action.payload) item.amount++
            }

            return {
                ...state,
                ingredients: newingredients,
                totalPrice: newtotalPrice + INGREDIENTS_PRICE[action.payload]
            }


        case actionTypes.REMOVE_INGREDIENTS:

            for (let item of newingredients) {
                if (item.type === action.payload) {
                    // console.log(item.amount)
                    if (item.amount <= 0) {
                        return {
                            ...state,
                        }
                    } else {
                        item.amount--
                    }
                }
            }
            return {
                ...state,
                ingredients: newingredients,
                totalPrice: newtotalPrice - INGREDIENTS_PRICE[action.payload]
            }
        case actionTypes.UPDATE_CANORDER:
            const sum = newingredients.reduce((sum, element) => {
                return sum += element.amount
            }, 0)

            return {
                ...state,
                canOrder: sum > 0
            }
        case actionTypes.RESET_BURGER:
            return {
                ...state,
                ingredients: [
                    { type: 'salad', amount: 0 },
                    { type: 'meat', amount: 0 },
                    { type: 'cheese', amount: 0 }
                ],
                totalPrice: 80,
                canOrder: false,
                ingredientsPrices: INGREDIENTS_PRICE
            }
        case actionTypes.LOAD_ORDER:

            let orders = []
            for (let key in action.payload) {
                orders.push({
                    ...action.payload[key],
                    id: key
                })
            }
            return {
                ...state,
                orders: orders,
                orderLoading: false,
                orderError: false
            }
        case actionTypes.ORDER_LOADING_FAILED:

            return {
                ...state,
                orderLoading: false,
                orderError: true,
                orderFailedMess: action.payload,
                authFailedMsg: null
            }

        // Authentication 
        case actionTypes.AUTH_SUCCESS:

            return {
                ...state,
                token: action.payload.token,
                userId: action.payload.userId,
                authFailedMsg: null
            }
        case actionTypes.AUTH_LOGOUT:
            return {
                ...state,
                token: null,
                userId: null,
                authFailedMsg: null
            }
        case actionTypes.AUTH_LOADING:
            return {
                ...state,
                authLoading: action.payload
            }
        case actionTypes.AUTH_FAILED:
            return {
                ...state,
                authFailedMsg: action.payload
            }

        default:
            return state
    }


}

