import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Alert } from 'reactstrap'
import Order from './Order'
import Spinner from '../../Spinner/Spinner'

import { fetchOrder } from '../../../redux/actionCreators'

const mapStateToProps = state => {
    return {
        orders: state.orders,
        orderLoading: state.orderLoading,
        orderError: state.orderError,
        orderFailedMess: state.orderFailedMess,
        token: state.token,
        userId: state.userId

    }
}

const mapDispatchToProps = dispatch => {
    return {
        fetchOrder: (token, userId) => dispatch(fetchOrder(token, userId))
    }
}


export class Orders extends Component {

    componentDidMount() {
        this.props.fetchOrder(this.props.token, this.props.userId)

    }

    render() {
        let showOrder = null
        if (this.props.orderLoading) {
            return <Spinner />
        } else if (this.props.orderError) {
            showOrder = <Alert color="warning"> {this.props.orderFailedMess
            } </Alert>
        }
        else {
            showOrder = this.props.orders.map(order => {
                return <Order key={order.id} order={order} />
            })
        }


        return (
            <div>
                {showOrder}
            </div>
        )
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Orders)